import Vue from 'vue'
import Router from 'vue-router'
import Article from '../components/router-pages/article.vue'
import Portfolio from '../components/router-pages/portfolio.vue'
import Project from '../components/router-pages/project.vue'
import Statistic from '../components/router-pages/statistic.vue'
import Comment from '../components/router-pages/comment.vue'
import About from '../components/router-pages/about.vue'
import Site from '../components/router-pages/site.vue'

import Archive from '../components/archive/archive.vue'
import markEditor from '../components/Editor/markeditor.vue'
import articlePage from '../components/article-page/article-page.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Article',
      component: Article,
      children:[
        {
          path: '/',
          name: 'articlePage',
          component: articlePage,
        },
        {
          path:'lists',
          component:Archive
        },
        {
          path:'editor',
          component:markEditor
        }
      ]
    },
    {
      path: '/article',
      name: 'Article',
      component: Article,
      children:[
        {
          path: '/',
          name: 'articlePage',
          component: articlePage,
        },
        {
          path:'lists',
          component:Archive
        },
        {
          path:'editor',
          component:markEditor
        }
      ]
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/project',
      name: 'Project',
      component:Project
    },
    {
      path: '/statistic',
      name: 'Statistic',
      component: Statistic
    },
    {
      path: '/comment',
      name: 'Comment',
      component: Comment
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/site',
      name: 'Site',
      component: Site
    },

  ]
})



