<template>
  <div>
    <div id='test' :option="option" ref="lineCharts"></div>
  </div>
</template>
<script>
  import HighCharts from 'highcharts'
  export default {
    // 验证类型
    data(){
      return{
        option: {
          chart: {
            type: 'spline'
          },
          title: {
            text: 'Monthly Average access'
          },
          subtitle: {
            text: 'Source: tongji.baidu.com'
          },
          xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
              'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
          },
          yAxis: {
            title: {
              text: 'accessors'
            },
            labels: {
              formatter: function () {
                return this.value;
              }
            }
          },
          tooltip: {
            crosshairs: true,
            shared: true
          },
          plotOptions: {
            spline: {
              marker: {
                radius: 4,
                lineColor: '#666666',
                lineWidth: 1
              }
            }
          },
          series: [{
            name: 'blog',
            marker: {
              symbol: 'square'
            },
            data: [7, 6, 9, 14, 18, 21, 25, 26, 23, 18, 13, 9]

          }, {
            name: 'cms',
            marker: {
              symbol: 'diamond'
            },
            data: [3, 4, 5, 8, 11, 15, 17, 16, 14, 10, 6, 4]
          }]
        }
      }
    },
    mounted() {
      HighCharts.chart(this.$refs.lineCharts,this.option)
    }
  }
</script>



