<template>
  <div>
    <div id='test3' :option="option" ref="tdCharts"></div>
  </div>
</template>
<script>
  import HighCharts from 'highcharts'
  export default {
    // 验证类型
    data(){
      return{
        option: {
          chart: {
            type: 'column',
            options3d: {
              enabled: true,
              alpha: 10,
              beta: 25,
              depth: 70
            }
          },
          title: {
            text: 'articles statistics'
          },
          subtitle: {
            text: 'Notice the difference between a 0 value and a null point'
          },
          plotOptions: {
            column: {
              depth: 25
            }
          },
          xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
              'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
          },
          yAxis: {
            title: {
              text: null
            }
          },
          series: [{
            name: 'Articles',
            data: [1, 3, null, 4, 0, 5, 1, 4, 6, 3]
          }]
        }
      }
    },
    mounted() {
      HighCharts.chart(this.$refs.tdCharts,this.option)
    }
  }
</script>



