import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
  state:{
    total:0,
    //article  list 页面数据
    article:{
      _id:'',
      title:'无标题文章',
      author:'Jekyll',
      content:'',
      describe:'just for record',
      create_time:'',
      label:'html',
      alter_time:'',
      href:'www.baidu.com',
      type:'原创',
      access_time:1
    },
    //article list page information
    pageInfo:{
      currentPage:1,
      pageSize:15
    },
    //topbar内tag
    topbarTags:[],
    curTag:''
  },
  mutations:{
    updateTotal(state,value){
      state.total = value
    },
    edit(state,editArticle){  //点击编辑，触发
      state.article = editArticle
    },
    updateTitle(state,nTitle){
      state.article.title = nTitle
    },
    update(state,value){
      state.article.content = value
      if(!state.article.create_time){
        state.article.create_time = (new Date()).toLocaleDateString()
      }else{
        state.article.alter_time = (new Date()).toLocaleDateString()
      }
    },
    //发布完成后，清空ID
    clear(state){
      state.article = {
        _id:'',
        title:'无标题文章',
        author:'Jekyll',
        content:'',
        describe:'just for record',
        create_time:'',
        label:'html',
        alter_time:'',
        href:'www.baidu.com',
        type:'原创',
        access_time:1
      }
    },
    updateCurTag(state,tag){
      state.curTag = tag
    },
    addTag(state,tag){  //添加顶部标签
      if(state.topbarTags.indexOf(tag)===-1){
          state.topbarTags.push(tag)
      }
    },
    removeTag(state,tag){  //添加顶部标签
      let index=state.topbarTags.indexOf(tag)
      if(index!==-1){
        state.topbarTags.splice(index,1)
      }
    }
  }
})



