<template>
  <div id="article-page">
      <div class="navigators">
        <div class="navigators-header"><i class="el-icon-setting"></i>navigators</div>
        <div>
          <buttons></buttons>
        </div>
      </div>
      <div class="simple-modules">
        <div class='visit-statistic'>
          <div class="visit-header"><i class="el-icon-my icon-statistic"></i>statistic</div>
          <chart1></chart1>
        </div>
        <div class="simple-module-item todo">
          <div class="module-header"><i class="el-icon-date"></i> todo</div>
          <div>
            <to-do></to-do>
          </div>
        </div>

      </div>
  </div>
</template>

<script>
import ToDo from './todo.vue'
import Buttons from './buttons.vue'
import Chart1 from '../charts/test.vue'
    export default {
        data() {
            return {}
        },
      components:{
        ToDo,Buttons,Chart1
      },
      created(){
        this.getStatistic()
      },
      methods:{
        getStatistic(){
          this.$http({
            method:'get',
            url:'/admin/article/statistic',
            params:{},
          }).then(
            //返回数据需包含，根据pageSize及currentPage筛选后的数据，令包含total
            res=> this.$store.commit('updateTotal',res.data.result))
        }
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  @import "../../common/stylus/icons.styl"
  .navigators
    width 95%
    margin 20px auto
    background-color: #f7f7f7
    border-radius 6px
  .navigators-header
    border-bottom 1px solid #b7b7b7
    font-size 16px
    color #737373
    padding 10px
    i
      margin-right 4px
  .simple-modules
    margin-top 32px
    display: flex
    justify-content space-around
    .todo
        border-radius 4px
        width 320px
        background url('http://repsitory.oss-cn-shanghai.aliyuncs.com/tower.jpg') no-repeat
        background-size cover
    .visit-statistic
      width 800px
      .visit-header
        background-color: #f7f7f7
        padding 10px
        border-bottom 1px solid #b7b7b7
  .module-header
      border-bottom 1px solid #b7b7b7
      font-size 16px
      color #ddd
      padding 10px


</style>



