<template>
  <div id ="portfolio">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="项目名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="技术选择">
        <el-select v-model="form.region" placeholder="请选择项目技术栈">
          <el-option label="Angular" value="Angular"></el-option>
          <el-option label="React" value="React"></el-option>
          <el-option label="Vue" value="Vue"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="项目时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="开源">
        <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="项目性质">
        <el-checkbox-group v-model="form.type">
          <el-checkbox label="萌新初识新技术" name="type"></el-checkbox>
          <el-checkbox label="日常手痒滚键盘" name="type"></el-checkbox>
          <el-checkbox label="闲得发慌造轮子" name="type"></el-checkbox>
          <el-checkbox label="手持两把锟斤拷" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="组成人员">
        <el-radio-group v-model="form.resource">
          <el-radio label="个人项目"></el-radio>
          <el-radio label="团队项目"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="项目说明">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-popover
          ref="popover"
          placement="top"
          width="160"
          v-model="visible">
          <p>我劝你还是不要乱点，因为我没做这个功能</p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="visible = false">取消</el-button>
            <el-button type="primary" size="mini" @click="visible = false">确定</el-button>
          </div>
        </el-popover>
        <el-button type="primary" v-popover:popover>立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
    <a href="http://www.kulolo.top/portfolio" target="_blank">跳转至页面</a>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #portfolio
    width 700px
    margin 40px auto
</style>



