<template>
  <el-amap vid="amapDemo" :plugin="plugins">
    <el-amap-marker v-for="(marker,index) in markers" :position="marker.position" :key="index">
    </el-amap-marker>
  </el-amap>
</template>

<script>
  export default {
    data() {
      return {
        markers: [],
        plugins: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType']
      }
    },
    mounted() {
      this.markers = [
        {
          position: [121.4963285, 31.26515044]
        }
      ];
    }
  }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>

</style>



