<template>
  <div id="charts">
    <div class="chart-item">
      <chart1></chart1>
    </div>
    <div class="chart-item">
      <chart2></chart2>
    </div>
    <div class="chart-item">
      <chart3></chart3>
    </div>
    <div class="chart-item">
      <chart4></chart4>
    </div>

  </div>
</template>

<script>
import Chart1 from '../charts/test.vue'
import Chart2 from '../charts/test2.vue'
import Chart4 from '../charts/test4.vue'
import Chart3 from '../charts/test3.vue'
    export default {
        data() {
            return {}
        },
      components:{
          Chart1,Chart2,Chart4,Chart3
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #charts
    width 100%
    display: flex
    flex-wrap wrap
    .chart-item
      width 49%
      min-width 40%

</style>



