        <p>利用下班的时间，断断续续的把这个demo完成了80%，在这里记录一下。前端部分基本就这样了，在做的过程中，发现最头疼的不是技术问题，因为我这水平所遇到的技术问题，在
        网上都能找的到解决方式。最头疼的时没有一个完整的设计，做的过程完全是想到哪里写到哪里，最终就从project变成了demo,demo就demo吧，起码可以发博客了。</p>
      </div>
      <ul>
        <li><a href="https://github.com/ss9501/blog-cms" target="_blank">前端代码地址</a></li>
        <li><a href="https://github.com/ss9501/blog-cms-backend" target="_blank">后端Node代码地址</a></li>
        <li><a href="http://www.kulolo.top" target="_blank">博客地址</a></li>
      </ul>
    </div>
  </div>
</template>

<script>

    export default {
        data() {
            return {}
        }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #projects
    height 50%
    width 70%
    margin 40px auto
    border-radius 5px
    background-color: #e7e7e7
