<template>
  <div id="sideBar">
    <div @click="spread">
      <div class="collapse-controller">
        <i :class="iconClass"></i>
      </div>
    </div>
    <el-menu mode="vertical"  class="el-menu-vertical" theme="dark" :collapse="isCollapse" :router=true>

      <side-item :name="'article'"></side-item>
      <side-item :name="'portfolio'"></side-item>
      <side-item :name="'project'"></side-item>
      <side-item :name="'statistic'"></side-item>
      <side-item :name="'comment'"></side-item>
      <side-item :name="'about'"></side-item>
      <side-item :name="'site'"></side-item>
    </el-menu>

  </div>

</template>

<script>
  import sideItem from './sideItem.vue'
  export default {
    data() {
      return {
        isCollapse: true,
        iconClass:'el-icon-arrow-right'

      };
    },
    components:{
      sideItem
    },
    methods: {
      spread(){
        if(this.iconClass==="el-icon-arrow-right"){
          this.iconClass="el-icon-arrow-left"
        }else{
          this.iconClass="el-icon-arrow-right"
        }
        this.isCollapse = !this.isCollapse
      }
    }
  }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>

  #sideBar
    position: relative
    height:100%
    .collapse-controller
      position: absolute
      width 50px
      height 50px
      z-index:999
      right -50px
      display: flex
      justify-content center
      align-items center
  .el-menu-vertical
    height:100%
  .el-menu-vertical:not(.el-menu--collapse)
    width: 200px;


</style>



