<template>
  <div id="about">
    <div class="cover"></div>
    <div class="introduction">
        <div class="bg">
          <h2>个人博客后台管理</h2>
          <hr/><br>
          <p>基于Vue全家桶及Element UI的简便管理系统</p>
          <br><br>
          <h3>背景</h3><br>
          <p>在阿里云上部署一个个人博客，为了便文章的编辑及添加，做了一个简易的管理系统。</p><br>
          <h3>使用技术</h3><br>
          <p>Vue全家桶，<a href="https://github.com/hinesboy/mavonEditor">mavonEditor</a> 插件，<a href="https://github.com/ElemeFE/element">Element UI</a>组件。</p>
          <p> 服务端使用Node.js+MongoDB实现了登录注销、文章的增删改功能。另外已有初步设计但还没实现的文件及图片上传、访问统计，待逐步添加。</p>
        </div>
    </div>

  </div>
</template>

<script>

    export default {
        data() {
            return {}
        }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #about
    box-sizing border-box
    border 10px solid #fff
    height 100%
    background url(http://repsitory.oss-cn-shanghai.aliyuncs.com/blog/about.jpg) no-repeat
    background-size cover
    background-clip border-box
    position: relative
    .cover
      position: absolute
      height 100%
      width 100%
      background-color: #111
      opacity .5
      z-index 1
    .introduction
      position absolute
      padding 20px
      width 80%
      background-color: #000
      opacity .6
      left 100px
      top 160px
      color #fff
      z-index 2
      p
        line-height 1.25
</style>



