<template>
  <ul id="btns">
    <li class="lists-page btn-item">
      <router-link to="/lists">
        <div @click="addTag" data-name="lists" class="btn-item-box item1">
          <div class="box-icon">
            <svg class="icon icon-list" aria-hidden="true">
              <use xlink:href="#icon-list"></use>
            </svg>
          </div>
          <div class="box-letter">
            <div>Article Lists</div>
            <div><span>total: {{total}}</span></div>
          </div>
        </div>
      </router-link>
    </li>
    <li class="editor-page btn-item" >
      <router-link to="/editor">
        <div @click="addTag" data-name="editor" class="btn-item-box item2">
          <div class="box-icon">
            <svg class="icon icon-add" aria-hidden="true">
              <use xlink:href="#icon-add"></use>
            </svg>
          </div>
          <div class="box-letter">
            <div>New Article</div>
            <div><span>markdown</span></div>
          </div>
        </div>
      </router-link>
    </li>
    <li class="site-visits btn-item" >
      <router-link to="/visit">
        <div @click="addTag" data-name="visits" class="btn-item-box item3">
          <div class="box-icon">
            <svg class="icon icon-visit" aria-hidden="true">
              <use xlink:href="#icon-visit"></use>
            </svg>
          </div>
          <div class="box-letter">
            <div>Site visits</div>
            <div><span>total:66</span></div>
          </div>
        </div>
      </router-link>
    </li>
    <li class="site-visits btn-item" >
      <a href="http://www.kulolo.top" target="_blank">
        <div class="btn-item-box item4">
          <div class="box-icon">
            <svg class="icon icon-homepage" aria-hidden="true">
              <use xlink:href="#icon-homepage"></use>
            </svg>
          </div>
          <div class="box-letter">
            <div>Home page</div>
            <div><span>blog</span></div>
          </div>
        </div>
      </a>
    </li>
  </ul>
</template>

<script>

    export default {
        data() {
            return {}
        },
      computed:{
        total(){
          return this.$store.state.total
        }
        },
        methods: {
          addTag(e) {
            this.$store.commit('addTag', e.currentTarget.dataset.name)
            this.$store.commit('updateCurTag', e.currentTarget.dataset.name)
          }
        }

    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #btns
    display: flex
    justify-content center
    padding 10px 0
  .btn-item-box
    display: flex
    padding 16px 16px
    border 1px solid #f7f7f7
    color #f7f7f7
    border-radius 4px
    margin  0 30px
  a
    text-decoration none
  .icon
    width 40px
    height 40px
    fill #f7f7f7
  .box-letter
    margin-left 8px
    display: flex
    flex-direction column
    justify-content space-between
    span
      font-size 12px

  .item1
    background-color: #6CAFE4
  .item2
    background-color: #59D18A
  .item3
    background-color: #63D1E2
  .item4
    background-color: #FD7B7B
</style>



