<template>
  <div>
    <div id='test' :option="option" ref="pieChart"></div>
  </div>
</template>
<script>
  import HighCharts from 'highcharts'
  export default {
    // 验证类型
    data(){
      return{
        option: {
          title: {
            text: 'Combination chart'
          },
          xAxis: {
            categories: ['Jun', 'Jul','Aug','Sep','Oct' ]
          },
          labels: {
            items: [{
              html: 'Total articles statistic',
              style: {
                left: '50px',
                top: '18px',
                color: 'black'
              }
            }]
          },
          series: [{
            type: 'column',
            name: 'HTML',
            data: [1, 2, 1, 3, 2]
          }, {
            type: 'column',
            name: 'CSS',
            data: [2, 1, 2,2,3]
          }, {
            type: 'column',
            name: 'JavaScript',
            data: [1, 1, 3, 2, 4]
          }, {
            type: 'spline',
            name: 'Average',
            data: [2, 1, 2, 2, 3],
            marker: {
              lineWidth: 2,
              lineColor: '#7798BF',
              fillColor: 'white'
            }
          }, {
            type: 'pie',
            name: 'Total articles',
            data: [{
              name: 'HTML',
              y: 13,
              color: '#90EE7E' // Jane's color
            }, {
              name: 'CSS',
              y: 23,
              color: '#7CB5EC' // John's color
            }, {
              name: 'JavaScript',
              y: 19,
              color: '#F7A35C' // Joe's color
            }],
            center: [100, 80],
            size: 100,
            showInLegend: false,
            dataLabels: {
              enabled: false
            }
          }]
        }
      }
    },
    mounted() {
      HighCharts.chart(this.$refs.pieChart,this.option)
    }
  }
</script>



