<template>
    <div id="editor">
      <div class="issue">
        <input type="text" :value="title" class="title" @input="updateTitle">
        <el-button type="info" @click="issue">发布文章</el-button>
      </div>
      <mavon-editor style="width:100%" :value="content" @change="update" ></mavon-editor>
    </div>
</template>

<script>
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'

  export default {
    components: {
      mavonEditor
    },
    data() {
      return {

      }
    },
    computed:{
      article(){
        return this.$store.state.article
      },
      title(){
        return this.$store.state.article.title
      },
      content(){
        return this.$store.state.article.content
      }
    },
    methods:{
      openSuccess() {
        this.$notify({
          title: '成功',
          message: '文章发布成功!',
          type: 'success',
          duration:2500
        });
      },
      updateTitle(e){
        this.$store.commit('updateTitle',e.target.value)
      },
      update(e){
        this.$store.commit('update',e)
      },
      clear(){
        this.$store.commit('clear')
      }
      ,
      issue(){  //文章上传至数据库
        this.$http({
          method:'post',
          url:'/admin/article/issue',
          data:this.article
        }).then(response=>{
          //console.log(response);
          this.openSuccess()
          this.clear()
        })
      }
    }

  }
</script>

<style lang="stylus" scoped >
  #editor
    margin:18px auto 8px
    width:1100px
  .issue
    display: flex
    justify-content space-between
    margin-bottom 12px
    padding 0 24px
  .title
    width 100%
    border 0
    outline none
    display: block
    font-size 32px
</style>



