<template>
  <div id="topbar">
      <div class="topbar-container">
        <div class="tag-area">
          <div  v-for="(item,index) in tags">
            <topbar-tag :tagName="item" :class="{'active':item===curTag}"></topbar-tag>
          </div>
        </div>

        <div class="topbar-search">
          <form action="" class="topbar-search-form">
            <label for="search-btn">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-search"></use>
              </svg>
            </label>
            <input type="submit" class="search-btn" id="search-btn" @click="">
            <input type="text"  class="search-text" placeholder="Search" >
          </form>
        </div>

        <div class="topbar-sign">
            <div class="exit">
              <el-tooltip class="item" effect="light" content="just do IT" placement="bottom">
              <svg class="icon icon-search" aria-hidden="true" @click="logOut">
                <use xlink:href="#icon-exit"></use>
              </svg>
              </el-tooltip>
            </div>
            <div class="tips">
              <el-badge :value="-1" class="item">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-ring"></use>
              </svg>
              </el-badge>
            </div>
          <div class="nick-name">admin</div>
            <div class = 'avatar'>

              <img src='http://repsitory.oss-cn-shanghai.aliyuncs.com/blog/ironman_meitu_1.jpg'   onerror="this.src='http://via.placeholder.com/32x32'" alt="">
            </div>
        </div>

      </div>
  </div>
</template>

<script>
  import topbarTag from '../../components/topbar-tag/tag.vue'

    export default {
        data() {
            return {}
        },
        computed:{
          tags(){
            return this.$store.state.topbarTags
          },
          curTag(){
            return this.$store.state.curTag
          }
        },
        components:{
          topbarTag
        },
        methods:{
            logOut(){
                this.$http({
                  method:'get',
                  url:'/admin/logout'
                }).then(
                  res=> {
                    if(res.data.status ==='0'){
                      window.location.href='/admin'
                    }
                  })
              }

        }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  @import '../../common/stylus/base.styl'
  #topbar
    padding 8px  48px
    background-color: #fff
    .topbar-container
      margin 0 auto
      display flex
      align-items center
      justify-content flex-end
      .tag-area
        width 100%
        display: flex
        justify-content flex-start
        overflow hidden
        >div
          margin-left 8px
  .topbar-search-form
    display: flex
    justify-content center
    align-items center
    .search-btn
      display none
    label
      display: flex
      align-items center
      justify-content center
      width 32px
      height 32px
      &:hover
        background-color: #ffe87c

    .search-text
       display: block
       height 32px
       border none
       outline none
       background-color: #f8f8f8

  .topbar-sign
    display: flex
    justify-content flex-end
    >div
      margin-left 12px
    .exit,.tips
      display: flex
      align-items center
     .tips
       margin-right 8px
    .nick-name
      color #a7a7a7
      font-size 14px
      height 14px
      margin-top 8px
    .avatar
      width 32px
      height 32px
      border-radius 50%
      overflow hidden
      margin-left 8px
      img
        width 32px
        height 32px
</style>






