<template>
  <el-menu-item :index="'/'+name" :tag="name" @click="addTag">
    <i :class="['el-icon-my','icon-'+name]"></i>
    <span slot="title">{{name}}</span>
  </el-menu-item>
</template>

<script>

    export default {
        props:['name'],
        data() {
            return {}
        },
        methods:{
          addTag(e){

            this.$store.commit('addTag',e.$attrs.tag)
            this.$store.commit('updateCurTag',e.$attrs.tag)
          },
        }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  @import "../../common/stylus/icons.styl"
</style>



